
function checkDataLayer() {
    try {
        window.dataLayer = window.dataLayer || [];
        errorCode = "";
        arguments = {
            event: 'workflowStep',
            workflowName: 'connectWallet',
            workflowStepNumber: 1,
            workflowStepName: 'start',
            workflowCompleteFlag: 0,
            workflowErrorCode: { errorCode }
        };
        console.log("dev-check-data-layer-", arguments);
        window.dataLayer.push(arguments);
    } catch (e) {
        console.error(`dev:checkDataLayer failed`);
        console.error(e.message);
    }
}
function hitDataLayer(walletAddress) {
    try {
        window.dataLayer = window.dataLayer || [];
        arguments = {
            event: 'workflowStep',
            workflowName: 'connectWallet',
            workflowStepNumber: 2,
            workflowStepName: 'successful',
            workflowCompleteFlag: 1,
            walletAddress: { walletAddress },
            workflowErrorCode: { errorCode }
        };
        console.log("dev-hit-data-layer", arguments);
        console.log(window.dataLayer.push(arguments));
    } catch (e) { console.log(e.message); }
}

function swapAmountDataLayer(e) {
    try {
        window.dataLayer = window.dataLayer || [];
        errorCode = "";
        arguments = {
            event: 'workflowStep',
            workflowName: 'swap',
            workflowStepNumber: 1,
            workflowStepName: 'swapAmount',
            workflowCompleteFlag: 0,
            workflowErrorCode: { errorCode },
            walletAddress: { walletAddress: e },
        };
        console.log("dev-hit-data-layer", arguments),
            console.log(window.dataLayer.push(arguments));
    } catch (e) {
        console.error(`dev:swapAmountDataLayer failed`);
        console.error(e.message);
    }
}

function confirmTransactionDataLayer(userAddress) {
    try {
        window.dataLayer = window.dataLayer || [];
        window.dataLayer.push({
            event: "workflowStep",
            workflowName: "swap",
            workflowStepNumber: 3,
            workflowStepName: "confirmTransaction",
            workflowCompleteFlag: 0,
            workflowErrorCode: { errorCode },
            walletAddress: { userAddress },
        });
    } catch (e) {
        console.error(`dev:confirmTransactionDataLayer failed`);
        console.error(e.message);
    }
}

function swapSuccessfulDataLayer(walletAddress, transactionId, BNBValue, IBATValue) {
    // so the swapFromCurrency is BNB, and swapFromValue is how much BNB is being swapped, and swapToCurrency is IBAT, and swapToValue is the value of IBAT being swapped
    try {
        window.dataLayer = window.dataLayer || [];
        window.dataLayer.push({
            event: "workflowStep",
            workflowName: "swap",
            workflowStepNumber: 3,
            workflowStepName: "swapSuccessful",
            workflowCompleteFlag: 1,
            workflowErrorCode: { errorCode },
            walletAddress: { walletAddress },
            transactionId: { transactionId },
            swapFromCurrency: "BNB",
            swapFromValue: { BNBValue },
            swapToCurrency: "IBAT",
            swapToValue: { IBATValue }
        });
    } catch (e) {
        console.error(`dev:swapSuccessfulDataLayer failed`);
        console.error(e.message);
    }
}